@keyframes upthenscale {
  0% {
    transform: translateY(250px);
  }

  50% {
    transform: translateY(0);
  }

  60% {
    width: 90px;
    height: 100px;
    transform: translateY(0);
  }

  100% {
    width: 100%;
    height: 250px;
    transform: translateY(0);
  }
}

@keyframes scalethendown {
  0% {
    width: 100%;
    height: 250px;
    transform: translateY(0);
  }

  50% {
    width: 90px;
    height: 100px;
    transform: translateY(0);
  }

  60% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(250px);
  }
}

@keyframes waitfade {
  0%, 50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes wiggle {
  0% {
    transform: translateY(310px)rotate(0);
  }

  25% {
    transform: translateY(310px)rotate(2deg);
  }

  50% {
    transform: translateY(310px)rotate(0);
  }

  75% {
    transform: translateY(310px)rotate(-2deg);
  }

  100% {
    transform: translateY(310px)rotate(0);
  }
}

.letter {
  max-width: 500px;
  height: 375px;
  display: block;
  position: relative;
  overflow: hidden;
  margin: 40px auto !important;
}

.letter--open .paper {
  animation-name: upthenscale;
  animation-duration: .6s;
  animation-fill-mode: forwards;
}

.letter--close .paper {
  animation-name: scalethendown;
  animation-duration: .4s;
  animation-fill-mode: forwards;
}

.letter--open .paper-content {
  animation-name: waitfade;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
}

.letter--open .envelope {
  animation: pause;
}

.paper-content {
  color: #545454;
  opacity: 0;
  padding: 25px;
  font-family: Roboto, sans-serif;
  font-weight: 700;
}

.paper {
  background: #f1f0c3;
  border-radius: 2px;
  width: 90px;
  height: 50px;
  margin: 0 auto;
  transition: transform .3s .1s;
  position: relative;
  transform: translateY(250px);
}

.envelope {
  z-index: 2;
  cursor: pointer;
  background: #f36363;
  border-radius: 3px;
  width: 94px;
  height: 60px;
  margin: auto;
  transition: box-shadow .3s;
  animation: .3s .2s infinite wiggle;
  position: relative;
  transform: translateY(310px);

  &:hover {
    animation: pause;

    & .envelope-paper {
      transform: translateY(-20px);
    }

    & .envelope-flap {
      transform: translateY(-45px);
    }

    & .envelope-detail:after {
      visibility: hidden;
    }
  }
}

.envelope-paper {
  background: #f1f0c3;
  border: 2px solid #0000;
  border-radius: 2px;
  width: 90px;
  height: 50px;
  margin: 0 auto;
  transition: transform .3s .1s;
  position: absolute;
  transform: translateY(0);
}

.letter--open .envelope-paper, .letter--open .envelope-detail:after, .letter--close .envelope-paper {
  visibility: hidden;
}

.envelope-flap {
  content: "";
  border-bottom: 45px solid #f36363;
  border-left: 45px solid #0000;
  border-right: 45px solid #0000;
  width: 0;
  height: 0;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(0);
}

.letter--open .envelope-flap {
  transform: translateY(-45px);
}

.letter--open .envelope-detail {
  border-top: 2px solid #e25b5b;
}

.envelope-detail {
  background: #f36363;
  border-top: 2px solid #0000;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  overflow: hidden;

  &:before {
    content: "";
    background: #f36363;
    border: 4px solid #e65454;
    width: 70px;
    height: 70px;
    margin: auto;
    display: block;
    position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    transform: rotate(45deg);
  }

  &:after {
    content: "";
    background: #f36363;
    border: 4px solid #e65454;
    width: 70px;
    height: 70px;
    margin: auto;
    display: block;
    position: absolute;
    top: -45px;
    left: 0;
    right: 0;
    transform: rotate(45deg);
  }
}

.envelope__fold {
  background: #f36363;
  border: 4px solid #e65454;
  border-radius: 3px;
  width: 86px;
  height: 25px;
  margin: auto;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
}

.paper-close {
  cursor: pointer;
  display: inline-block;
  position: absolute;
  top: 5px;
  right: 10px;
}

@media (width <= 900px) and (width >= 601px) {
  .letter {
    max-width: 85%;
    height: auto;
    margin: 60px auto;
  }

  .paper {
    width: 80%;
    height: 100px;
    margin: 20px auto;
  }

  .envelope {
    width: 80%;
    height: auto;
    transform: translateY(0);
  }

  .envelope-paper {
    width: 80%;
    height: 100px;
    margin: 0 auto;
  }

  .envelope-flap {
    border-bottom: 30px solid #f36363;
    border-left: 30px solid #0000;
    border-right: 30px solid #0000;
  }

  .paper-content {
    padding: 20px;
    font-size: 16px;
  }

  .envelope__fold {
    width: 80%;
    height: 25px;
  }

  .paper-close {
    top: 5px;
    right: 10px;
  }
}

@media (width <= 756px) {
  .letter {
    max-width: 85%;
    height: auto;
    margin: 30px auto;
  }

  .paper {
    width: 70%;
    height: 80px;
    margin: 10px auto;
  }

  .envelope {
    width: 70%;
    height: auto;
    margin: 0 auto;
    transform: translateY(0);
  }

  .envelope-paper {
    width: 70%;
    height: 80px;
    margin: 0 auto;
  }

  .envelope-flap {
    border-bottom: 20px solid #f36363;
    border-left: 20px solid #0000;
    border-right: 20px solid #0000;
  }

  .paper-content {
    padding: 15px;
    font-size: 14px;
  }

  .envelope__fold {
    width: 70%;
    height: 20px;
  }

  .paper-close {
    top: 5px;
    right: 5px;
  }
}

